<template>
  <el-card>
    <template #header>
      <span>Clients</span>
      <el-button type="primary" plain @click="dialog = true"
        >Add New Client</el-button
      >
    </template>
    <el-dialog v-model="dialog" title="Add Client" :before-close="handleClose">
      <el-form
        :model="company_branch"
        size="large"
        ref="company_branch"
        label-position="top"
        :inline="true"
      >
        <el-row>
          <el-col :span="12">
            <el-form-item
              label="Business Name"
              prop="company_name"
              :rules="[
                {
                  required: true,
                  message: 'Business name is required',
                  trigger: ['blur', 'change'],
                },
              ]"
            >
              <el-input
                v-model="company_branch.company_name"
                placeholder="Enter business name"
              /> </el-form-item
          ></el-col>
          <el-col :span="12">
            <el-form-item
              label="Registration Number"
              prop="business_regno"
              :rules="[
                {
                  required: true,
                  message: 'Registration number is required',
                  trigger: ['blur', 'change'],
                },
              ]"
            >
              <el-input
                v-model="company_branch.business_regno"
                placeholder="Enter registration number"
              /> </el-form-item
          ></el-col>
          <el-col :span="12">
            <el-form-item label="Incorporation Date">
              <el-date-picker
                v-model="company_branch.incorporation_date"
                type="date"
                placeholder="Date of Incorporation"
                style="width: 100%"
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="KRA PIN">
              <el-input
                v-model="company_branch.kra_pin"
                placeholder="Enter kra pin"
              /> </el-form-item
          ></el-col>
          <el-col :span="12">
            <el-form-item label="NHIF Number">
              <el-input
                v-model="company_branch.nhif_number"
                placeholder="Enter NHIF number"
              /> </el-form-item
          ></el-col>
          <el-col :span="12">
            <el-form-item label="NSSF Number">
              <el-input
                v-model="company_branch.nssf_number"
                placeholder="Enter NSSF number"
              /> </el-form-item
          ></el-col>
          <el-col :span="12">
            <el-form-item
              label="Phone Number"
              prop="phone"
              :rules="[
                {
                  required: true,
                  message: 'Phone number is required',
                  trigger: ['blur', 'change'],
                },
              ]"
            >
              <el-input
                v-model="company_branch.phone"
                placeholder="Enter phone number"
              /> </el-form-item
          ></el-col>
          <el-col :span="12">
            <el-form-item
              label="Email Address"
              prop="email"
              :rules="[
                {
                  required: true,
                  message: 'Email address is required',
                  trigger: ['blur', 'change'],
                },
                {
                  type: 'email',
                  message: 'Please enter valid address',
                  trigger: ['blur', 'change'],
                },
              ]"
            >
              <el-input
                v-model="company_branch.email"
                placeholder="Enter email address"
              /> </el-form-item
          ></el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item label="Industry">
              <el-select
                v-model="company_branch.industry"
                placeholder="Select industry"
                filterable
              >
                <el-option
                  :value="industry.industry"
                  v-for="industry in $store.state.dictionary.industries"
                  :key="industry.industry"
                  :label="industry.industry"
                />
              </el-select> </el-form-item
          ></el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item label="Number of Employees">
              <el-select
                v-model="company_branch.company_size"
                placeholder="Select company size"
                filterable
              >
                <el-option
                  :value="size"
                  v-for="size in [
                    '1 - 5',
                    '5 - 10',
                    '10 - 20',
                    '20 - 100',
                    '100 +',
                  ]"
                  :key="size"
                  :label="size"
                />
              </el-select> </el-form-item
          ></el-col>
          <el-col :span="24">
            <el-form-item
              label="Country"
              prop="country"
              :rules="[
                {
                  required: true,
                  message: 'Country is required',
                  trigger: ['blur', 'change'],
                },
              ]"
            >
              <el-select
                v-model="company_branch.country"
                filterable
                placeholder="Select country"
              >
                <el-option
                  v-for="country in $store.state.dictionary.countries"
                  :key="country"
                  :label="country.name"
                  :value="country.name"
                />
              </el-select> </el-form-item
          ></el-col>
        </el-row>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="handleClose">Cancel</el-button>
          <el-button
            type="primary"
            plain
            @click="submit('company_branch')"
            :loading="loading"
            >Save</el-button
          >
        </span>
      </template>
    </el-dialog>

    <el-dialog
      v-model="upload_dialog"
      title="Upload Receipt"
      :before-close="handleClose"
    >
      <el-form
        :model="company_branch"
        size="large"
        ref="company_branch"
        label-position="top"
        inline
        status-icon
      >
        <el-row>
          <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <el-form-item label="" prop="company_logo">
              <el-upload
                accept="image/*"
                :auto-upload="false"
                :on-change="uploadLogo"
                drag
              >
                <i class="fas fa-cloud el-icon--upload" />
                <div class="el-upload__text">
                  Drop or click here to<em> upload company logo</em>
                </div>
              </el-upload>
              <el-progress
                v-if="loading"
                :percentage="50"
                status="warning"
                :indeterminate="true"
              />
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>

      <template #footer>
        <span class="dialog-footer">
          <el-button @click="handleClose">Cancel</el-button>
        </span>
      </template>
    </el-dialog>

    <el-table ref="tableRef" :data="company_branches" v-loading="loading">
      <el-table-column prop="company_name" label="Client" width="250">
        <template #default="scope">
          <router-link to="#" @click="dialog = true">
            <div style="display: flex; align-items: center">
              <el-avatar
                v-if="scope.row.company_logo"
                :src="scope.row.company_logo"
              />
              <el-avatar v-else>
                {{ $helpers.getInitials(scope.row.company_name) }}
              </el-avatar>
              <span style="margin-left: 10px"
                ><span class="text-black"
                  >{{ scope.row.company_name }}<br /></span
                >{{ scope.row.merchant_code }}</span
              >
            </div>
          </router-link>
        </template>
      </el-table-column>
      <el-table-column prop="jobs" label="Jobs">
        <template #default="props">
          <router-link to="/applications">{{ props.row.jobs }}</router-link>
        </template>
      </el-table-column>
      <el-table-column prop="applications" label="Applications">
        <template #default="props">
          <router-link to="/applications">{{ props.row.applications }}</router-link>
        </template>
      </el-table-column>
      <el-table-column prop="hired" label="Hired">
        <template #default="props">
          <router-link to="/employees">{{ props.row.hired }}</router-link>
        </template>
      </el-table-column>

      <el-table-column prop="action" label="Action" width="70">
        <template #default="props">
          <el-dropdown>
            <span class="el-dropdown-link">
              <i class="fas fa-ellipsis-v"></i>
            </span>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item @click="edit(props.row)"
                  >Edit</el-dropdown-item
                >
                <el-dropdown-item @click="deleteCompanyBranch(props.row.id)"
                  >Delete</el-dropdown-item
                >
                <el-dropdown-item
                  @click="(company_branch = props.row), (upload_dialog = true)"
                  >{{ props.row.receipt ? "Change Logo" : "Upload Logo" }}
                </el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
  </el-card>
</template>

<script>
import CompanyService from "../../services/company.service";
export default {
  data() {
    return {
      dialog: false,
      upload_dialog: false,
      loading: false,
      company_branch: {},
      file: "",
    };
  },
  computed: {
    company_branches() {
      this.loading = true;
      if (this.$store.state.company.company_branches != undefined) {
        this.loading = false;
        return this.$store.state.company.company_branches;
      }
    },
  },
  mounted() {
    this.$store.dispatch("dictionary/getCountries");
    this.$store.dispatch("dictionary/getIndustries");
    this.$store.dispatch("company/getCompanyBranches");
  },
  methods: {
    edit(data) {
      this.company_branch = data;
      this.dialog = true;
    },
    submit(validate) {
      this.$refs[validate].validate((valid) => {
        if (valid) {
          this.loading = true;
          this.$store
            .dispatch("company/saveCompanyBranch", this.company_branch)
            .then(
              (res) => {
                this.company_branch = {};
                this.$store.dispatch("company/getCompanyBranches");
                this.dialog = false;
                this.loading = false;
                this.$notify({
                  title: "Success",
                  message: res.data.message,
                  type: "success",
                });
              },
              (error) => {
                this.loading = false;
                this.message =
                  (error.response &&
                    error.response.data &&
                    error.response.data.message) ||
                  error.message ||
                  error.toString();
                this.$notify({
                  title: "Error",
                  message: this.message,
                  type: "error",
                });
              }
            );
        } else {
          return false;
        }
      });
    },

    deleteCompanyBranch(id) {
      if (confirm("Do you really want to delete this record?")) {
        this.$store
          .dispatch("company/deleteCompanyBranch", id)
          .then((response) => {
            this.$store.dispatch("company/getCompanyBranches");
            this.$notify({
              title: "Success",
              message: response.data.message,
              type: "success",
            });
          })
          .catch((error) => {
            this.loading = false;
            this.message =
              (error.response &&
                error.response.data &&
                error.response.data.message) ||
              error.message ||
              error.toString();

            this.$notify({
              title: "Error",
              message: this.message,
              type: "error",
            });
          });
      }
    },

    uploadLogo(e) {
      this.loading = true;
      this.file = e.raw;
      let formData = new FormData();
      formData.append("file", this.file);
      CompanyService.uploadLogo(this.company_branch.id, formData).then(
        (res) => {
          this.$store.dispatch("company/getCompanyBranches");
          this.loading = false;
          this.upload_dialog = false;
          this.$notify({
            title: "Success",
            message: res.data.message,
            type: "success",
          });
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.$notify({
            title: "Error",
            message: this.message,
            type: "error",
          });
        }
      );
    },
    handleClose() {
      this.dialog = false;
      this.upload_dialog = false;
      this.$nextTick(() => {
        this.company_branch = {};
        this.$store.dispatch("company/getCompanyBranches");
      });
    },
  },
};
</script>